export class EmojiProvider {

  getEmojis() {
    const EMOJIS = "                                    " +
      "                                        " +
      "                                         " +
      "                                      " +
      "                          " +
      "                               " +
      "                             " +
      "               " +
      "                               " +
      "   ";

    const EmojiArr = EMOJIS.split(' ');
    const groupNum = Math.ceil(EmojiArr.length / (6));
    const items = [];

    for (let i = 0; i < groupNum; i++) {
      items.push(EmojiArr.slice(i * 6, (i + 1) * 6));
    }

    return items
  }

}



